**  ANIMATIONS
*/
:root {
    --anim-duration: .8s;
    --slide-shift: 5vh;
}
body:not(.no-anim) {
    overflow-x:hidden;
}
body:not(.no-anim) .fade,
body:not(.no-anim) .slide {
    -webkit-transition-duration: var(--anim-duration);
            transition-duration: var(--anim-duration);
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    transition-property: opacity, transform;
    transition-property: opacity, transform, -webkit-transform;
}
body:not(.no-anim) .fade { opacity: 0;
}
body:not(.no-anim) .fade.done { opacity: 1;
}
body:not(.no-anim) .slide, 
body:not(.no-anim) .slide.up { -webkit-transform: translateY(var(--slide-shift)); transform: translateY(var(--slide-shift));
}
body:not(.no-anim) .slide.down { -webkit-transform: translateY(calc(var(--slide-shift) * -1)); transform: translateY(calc(var(--slide-shift) * -1));
}
body:not(.no-anim) .slide.left { -webkit-transform: translateX(var(--slide-shift)); transform: translateX(var(--slide-shift));
}
body:not(.no-anim) .slide.right { -webkit-transform: translateX(calc(var(--slide-shift) * -1)); transform: translateX(calc(var(--slide-shift) * -1));
}
body:not(.no-anim) .slide.done { -webkit-transform: translateX(0) translateY(0); transform: translateX(0) translateY(0);
}

